import React, { useEffect, useState } from "react";
import { FaArrowLeft, FaArrowRight, FaBars } from "react-icons/fa";
import { Link, useNavigate } from "react-router-dom";

import "../styles/Header.css";

const Header = () => {
  const Navigation = useNavigate();
  const LogOut = () => {
    localStorage.removeItem("user");
    Navigation("/");
  };
  // let token = localStorage.getItem("user");
  // useEffect(() => {
  //   if (!token) {
  //     Navigation("/login");
  //   }
  // }, []);
  const [Arrow, setArrow] = useState(true);
  return (
    <>
      <div className={Arrow ? "admin__header " : "active"}>
        <ul>
          <li>
            <Link className="dashboard__link" to={"manageproducts"}>
              Manage Products
            </Link>
          </li>
          <li>
            <Link className="dashboard__link" to={"postproducts"}>
              Post Products
            </Link>
          </li>
          <li>
            <Link className="dashboard__link" to={"managereviews"}>
              Manage Reviews
            </Link>
          </li>
          <li>
            <button onClick={LogOut}>Log Out</button>
          </li>
        </ul>
        <div className="header__arrow" onClick={() => setArrow(!Arrow)}>
          <span>{Arrow ? <FaArrowRight /> : <FaArrowLeft />}</span>
        </div>
      </div>
    </>
  );
};

export default Header;
